/* You can add global styles to this file, and also import other style files */

@import "../node_modules/pretty-checkbox/src/pretty-checkbox.scss";
@import "../node_modules/ngx-smart-modal/ngx-smart-modal.scss";

@import "./colors.scss";

@font-face {
    font-family: "Baloo Chettan 2";
    src: url("assets/BalooChettan2-Regular.ttf") format("truetype");
}

// General
html,
body {
    height: 100%;
    margin: 0;
    font-family: "Baloo Chettan 2", sans-serif;
}

// Colors
.color-primary {
    color: $primary;
}

h1,
h2,
h3,
h4,
h5 {
    color: $secondary;
}

p,
a,
span,
div {
    color: rgb(220, 220, 220);
}

// Layout
#outer-grey-div {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #28282f;
    min-height: 100vh;
    min-width: 100%;

    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
}

#inner-grey-div {
    background-color: #31313a8c;
    margin-top: 4vh;
    margin-bottom: 4vh;
    height: 92vh;
    width: 94vw;

    background: linear-gradient(-45deg, #7b3d2b, #832348, #155f7b, #147961);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
}

@media screen and (max-width: 420px) {
    #inner-grey-div {
        width: 100vh;
        min-height: 100vh;
        margin-top: 0px;
        margin-bottom: 0px;
    }
}

// Page Container
.page-container {
    min-height: 75vh;
}

@media screen and (min-width: 900px) {
    .page-container {
        min-width: 762px;
    }
}

// Heading
.heading {
    display: flex;
    justify-content: center;
    padding-bottom: 50px;
    padding-top: 50px;
}

h1 {
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: xx-large;
    font-weight: 600;
}

// Sub Heading
.sub-heading {
    border-bottom: solid;
    border-color: $third;
    border-width: thin;
    background-color: transparent;
    width: 100%;
    margin-bottom: 20px;
    margin-top: 20px;
}

.sub-heading h3 {
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 500;
}

@media screen and (max-width: 768px) {
    .sub-heading {
        text-align: center;
    }
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

.nsm-content {

    background: linear-gradient(-45deg, #7b3d2b, #832348, #155f7b, #147961); // inner animation
    // background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);      // outer animation
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
}

@media screen and (min-width: 600px) {
    .nsm-dialog {
        max-width: 75%;
    }
}

.nsm-dialog-btn-close {
    color: white;
}

.d-flex {
    display: flex;
}

.flex-row {
    flex-flow: row;
}

.flex-column {
    flex-flow: column;
}

.flex-wrap {
    flex-wrap: wrap;
}
